.card {
  padding: 1.25rem;
  margin: 2rem auto;
  background: white;
  border-radius: 20px;
  box-shadow: 0, 2px, 8 px rgb(138, 135, 135);
  max-width: 95%;
  width: 50rem;
}
.card label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

.card input{
  display: block;
  width: 100%;
  border: 1px solid rgb(231, 156, 43);
  font: inherit;
  line-height: 1.5rem;
  padding: 0 0.25rem;
}
.card input:focus {
  outline: none;
  background: whi#f1f162;
  border-color: #8b005d;
}


